.scroll-up {
  width: 60px;
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: #eeeeee;
  border: none;
  -webkit-transition: opacity ease 0.7s, background-color linear 0.1s;
  -o-transition: opacity ease 0.7s, background-color linear 0.1s;
  transition: opacity ease 0.7s, background-color linear 0.1s;
  opacity: 0; }
  .scroll-up:hover {
    background-color: #e0e0e0; }
  .scroll-up--show {
    opacity: 1;
    cursor: pointer; }
  .scroll-up:focus {
    border: none;
    outline: none; }
  .scroll-up__arrow-block {
    position: absolute;
    top: 20px;
    left: 18px;
    width: 25px;
    height: 25px;
    -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
            transform: rotate(45deg);
    border-top: 5px solid #d1d1d1;
    border-left: 5px solid #d1d1d1;
    -webkit-transition: border-color linear 0.1s;
    -o-transition: border-color linear 0.1s;
    transition: border-color linear 0.1s; }
  .scroll-up:hover .scroll-up__arrow-block {
    border-color: #aaaaaa; }



